<template>
    <v-sparkline
        :value="chartData"
        :smooth="radius || false"
        :padding="padding"
        :line-width="width"
        :stroke-linecap="lineCap"
        :fill="fill"
        :type="type"
        :auto-line-width="autoLineWidth"
        auto-draw
    >
        <template v-slot:label="item" v-if="showItem">
            ${{ item.value }}
        </template>
    </v-sparkline>
</template>
<script>


export default {
    name:'TableFieldSparkline',
    props: {
        field: String,
        showItem: Boolean,
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    data: () => ({
      width: 1,
      radius: 5,
      padding: 8,
      lineCap: 'round',
      fill: false,
      type: 'trend',
      autoLineWidth: true,
    }),
    computed: {
        chartData () {
            return this.item[this.header.value].map(e => e[this.field])
        }
    } 
}
</script>

